        <div class="top">
          <div class="top_left">
            <p class="score" style="font-size: 24px;">{{seller.score}}</p>
            <p></p>
            <p>{{seller.rankRate}}%</p>
          </div>
          <div class="top_right">
            <p>
              <span style="padding-right: 5px;"></span>
              <span v-for="item in stars" :class="item == 1 ? 'light' : 'dark'"></span>
              <span class="score">{{seller.serviceScore}}</span>
            </p>
            <p>
              <span style="padding-right: 5px;"></span>
              <span v-for="item in stars" :class="item == 1 ? 'light' : 'dark'"></span>
              <span class="score">{{seller.foodScore}}</span>
            </p>
            <p>
              <span style="padding-right: 5px;"></span>
              <span style="color: grey">{{seller.deliveryTime}}</span>
            </p>
          </div>
        </div>
        <ratingselect @onlyContentChange="onlyContentChange" @typeChange="typeChange" :onlyContent="onlyContent" :selectType="selectType" :ratings="ratings" style="margin: 0 18px;"></ratingselect>
        <div class="comment">
          <ul>
            <li class="rating-item" v-show="needShow(rating.rateType,rating.text)" v-for="(rating,index) in ratings">
              <div class="avatar">
                <img width="28" height="28" :src="rating.avatar" style="border-radius: 50%">
              </div>
              <div class="content">
                <p>
                  {{rating.username}}
                  <span style="float: right;color: grey;">{{rating.rateTime|format}}</span>
                </p>
                <p >
                  <span v-for="item in ratings_stars[index]" :class="item == 1 ? 'small_light' : 'small_dark'"></span>
                  <span style="color: grey;" v-show="rating.deliveryTime">{{rating.deliveryTime}}</span>
                </p>
                <p>{{rating.text}}</p>
