//            _this.ratings = response.data.data;
            _this.calRatingsStars();
//          }
//        }).catch((err)=>{
//          console.log(err);
//        });
      },
      updated(){//myscroll
        this.$nextTick(() => {
          this.myscroll.refresh();
        })
      }
    }
