<template>
  <div style="height: 100%">
    <div class="content">
      <div class="content-left" @click="toggleList">
        <div class="logo-wrapper">
          <span class="cart" :class="totalCount > 0 ? 'blue_cart' : 'gray_cart'"></span>
          <span class="num" v-show="totalCount > 0">{{totalCount}}</span>
        </div>
        <div class="price">
          <span :class="totalPrice > 0 ? 'white_font' : ''" style="border-right: 1px solid rgba(255,255,255,0.1);padding-right: 12px;">{{totalPrice}}</span>
        </div>
        <span class="deliveryPrice">{{seller.deliveryPrice}}</span>

      </div>
      <div v-for="ball in balls">
        <transition name="drop" @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
          <div class="ball" v-show="ball.show">
            <div class="inner inner-hook"></div>
          </div>
        </transition>
      </div>
      <div class="content-right">
        <div @click="pay" class="pay" :class="totalPrice >= seller.minPrice ? 'pay_active' : ''">{{payDescription}}</div>
      </div>
      <transition name="fold">
        <div class="shopcart-list" v-show="listShow">
          <div class="list-header">
            <span></span>
            <span class="empty" @click="emptyCart"></span>
          </div>
          <div class="list-content" ref="list-content">
            <ul>
              <li class="food" v-for="food in selectFoods" v-show="food.count > 0">
                <span class="name">{{food.name}}</span>
                <span class="price">{{food.price*food.count}}</span>
                <div class="cartcontrol-wrapper">
                  <cartcontrol :food="food"></cartcontrol>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </transition>
    </div>
    <transition name="fade">
      <div v-show="listShow" class="list-mask" @click="toggleList"></div>
    </transition>
  </div>
</template>

