<template>
    <div class="myheader">
      <div class="background">
        <img :src="seller.avatar" width="100%" height="100%">
      </div>
      <div class="content">
        <div style="padding-left: 5px;">
          <img width="64" height="64" :src="seller.avatar">
        </div>
        <div style="padding-left: 10px;">
          <p>
            <img src="../../brand@2x.png" alt="" id="brand">
            <span style="padding-left: 5px;font-weight: bold;font-size: 18px;">{{seller.name}}</span>
          </p>
          <p class="news">{{seller.description}}/{{seller.deliveryTime}}</p>
          <p v-if="seller.supports" class="news">
            <span v-if="seller.supports[0].type >= 0" class="icon" :class="'icon' + seller.supports[0].type"></span>
            <span class="text">{{seller.supports[0].description}}</span>
            <span class="count" @click="showDetail">{{seller.supports.length}} </span>
          </p>
        </div>
      </div>
      <div @click="showDetail" class="notice">
        <span class="notice_icon"></span>
        <span>{{seller.bulletin}}</span>
        <span style="position: absolute;right: 10px;"></span>
      </div>
      <transition name="fade">
        <div v-show="detailShow" class="detail">
          <div>
            <h3 style="margin-top: 30px;">{{seller.name}}</h3>
            <div style="margin: 15px auto;font-size: 14px;color: grey;width: 80%;">
              <span v-for="item in stars" :class="item == 1 ? 'light' : 'dark'"></span>
            </div>
            <div class="information">
                <span class="line"></span>
                <span style="padding: 0 12px;"></span>
                <span class="line"></span>
              </div>
            <ul style="margin-left: 35px;text-align: left;font-size: 14px;">
                <li v-for="(item,index) in seller.supports" style="height: 30px;line-height: 30px;">
                  <span v-if="seller.supports[index].type >= 0" class="icon" :class="'icon' + seller.supports[index].type" style="vertical-align: text-top;"></span>
                  <span class="text" style="margin-left: 5px;">{{seller.supports[index].description}}</span>
                </li>
              </ul>
            <div class="information">
                <span class="line"></span>
                <span style="padding: 0 12px;"></span>
                <span class="line"></span>
              </div>
            <div v-if="seller.bulletin">
                <p style="margin: 0 35px;text-align: justify;">{{seller.bulletin}}</p>
              </div>
            <span style="display: inline-block;margin-top: 50px;">
              <img src="../../close.png" alt="" style="width: 40px;height: 40px;" @click="closeDetail">
            </span>
          </div>
        </div>
      </transition>
    </div>
</template>

